.container {
  width: 100%;
  position: relative;
  margin-top: 20px;
}

.input {
  border-radius: 0;
  border: 2px solid black;
}

.input:focus {
  box-shadow: none;
  border: 2px solid black;
}

.button-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.remove-button {
  color: black;
  background-color: white;
  border: 2px solid black;
  border-radius: 80px;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 2px 10px;
}

.remove-button:hover {
  color: rgb(161, 12, 12);
  background-color: white;
  border: 2px solid rgb(161, 12, 12);
}